@import '../color-mixin/color-scheme';

.btn {
    @include btn-color-scheme;
    text-decoration: none;
    cursor: pointer;
    max-width: 150px;
    min-width: 38px;
    min-height: 38px;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    padding: 5px;
}